// zendesk-search styles
.zendesk-search {
  padding: 50px 0;
  text-align: center;
  background: white;

  form {
    width: 100%;
    margin: auto;
  }

  label {
    margin-top: 10px;
    color: #666;
    margin-right: 25px;
  }

  .input-wrapper {
    margin-top: 25px;
  }

  input {
    background-color: #eee;
    width: 50%;
    min-width: 150px;
    padding: 15px 10px 15px 15px;
    margin: 0;
    text-indent: 25px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    border: 2px solid #f6f6f6;
  }

  button {
    margin-left: -10px;
    padding: 2px 10px;
    top: -3px;
    position: relative;
    font-size: 16px;
    border-radius: 10px;
    background: #fc33f3;
    border: 0;
    color: white;

    img {
      width: 50px;
    }
  }

}


@media screen and (min-width: 768px) {

  .zendesk-search {
    .input-wrapper {
      margin-top: 0;
      display: inline;
    }

    button {
      top: -4px;
      padding: 3px 10px;
    }

    input {
      width: 400px;
    }
  }

}

@media screen and (min-width: 992px) {

  .zendesk-search {
    button{
      padding: 4px 10px;
    }
  }

}
