// footer styles
footer {
  position: relative;
  top: 75px;
  padding: 50px 0px 25px;
  background: #212121;

  li, p, a {
    color: #888;
  }

  p {
    font-size: 12px;
  }

  p.disclaimer {
    font-size: 11px;
  }

  li, a {
    font-size: 14px;
  }

  ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
  }

  li {
    margin-bottom: 2px;
  }

  div, nav {
    margin-bottom: 25px;
  }

  #subscribeForm {
    margin: 15px 0;
    font-size: 15px;
    color: #888;
    
    ::-webkit-input-placeholder {
       color: #888;
       font-size: 14px;
       font-style: italic;
    }

    :-moz-placeholder { /* Firefox 18- */
       color: #888;  
       font-style: italic;
    }

    ::-moz-placeholder {  /* Firefox 19+ */
       color: #888;  
       font-style: italic;
    }

    :-ms-input-placeholder {  
       color: #888;  
       font-style: italic;
    }


    .email-input {
      width: 175px;
      padding: 3px 7px;
    }

    input {
      border-radius: 5px;
      border: 2px solid #888;
      background: rgba(255, 255, 255, 0);
    }

    #submit {
      padding: 3px 9px;
      display: block;
      font-size: 12px;
      font-weight: bold;
      margin-top: 10px;
      &:hover {
        opacity: 0.5;
      }
    }
    
  }



}