// banner styles
.banner {
  text-align: center;
  
  .content-wrapper {
    padding: 30px 0 45px;
  }

  .icon-wrapper {
    text-align: center;
    display: inline;
  }

  img {
    display: inline;
  }

  img.icon {
    height: 40px;
    margin-right: 15px;
  }

  p {
    display: inline-block;
    margin-right: 15px;
    margin-bottom: 35px;
    font-size: 1.2em
  }

  h3 {
    display: inline-block;
    margin-right: 50px;
  }

  img.download-btn {
    position: relative;
    top: -5px;
    width: 200px;
  }

}


@media screen and (min-width: 992px) {
  .banner {
    .content-wrapper { padding: 35px 0; }
  }
}

@media screen and (max-width: 768px) {
  .banner {
    .icon-wrapper {
      display: block;
    }
    img.download-btn, a.button {
      display: block;
    }
    img {
      margin: auto;
      margin-bottom: 25px;
    }
    img.icon {
      height: 50px;
    }

  }
}