// faq styles

// Variant 9 - Apps Airplay: Check Marks
.faq-panel{
  padding: 25px 0 45px;
  h2 {
    text-align: center;
    margin-bottom: 50px;
  }
  .question-answer-container {
    float: left;
    width: 100%;
  }
  p {
    float: left;
    width: 100%;
  }
  .question {
    margin-top: 0;
    &:hover {
      cursor: pointer;
    }
    &:before {
      float: left;
      margin-right: 35px;
      position: relative;
      top: 0px;
      margin-bottom: 15px;
    }
  }
  .answer {
    display: none;
    margin-top: 0;
    margin-bottom: 50px;
    padding-left: 60px;

    a {
      color: white;
      text-decoration: underline;
      font-size: 1em;
    }
  }
}

.faq-panel.white {
  .question:before {
    content: url('../images/misc/small/plus-icon-white.png');
  }
}


.faq-panel.black {
  .question:before {
    content: url('../images/misc/small/plus-icon-black.png');
  }
}