// multi-column-panel styles

.multi-column-panel {
  text-align: center;
  padding: 75px 0 60px;

  h5, p {
    color: #333;
  }

  .column-wrapper {
    margin-bottom: 25px;
    &:last-of-type {
      margin-bottom: 10px;
    }
  }

  h2 { margin-bottom: 50px; }
  h4 { margin: 0px auto 50px auto; }
  h5 { margin-bottom: 0; font-weight: bold; }

  img {
    display: block;
    margin: auto;
    text-align: center;
  }

  img.multi-col-img, .multi-col-icon {
    max-width: 100%;
    width: auto;
    height: 150px;    
  }

  img.multi-col-img-large {
    width: 100%;
    margin-bottom: 35px;
  }

  .body-text {
    padding: 0 15px;
  }

  a {
    font-weight: bold;
  }

  .five-col {
    width: 100%;
    float: left;
    h5.title {
      margin-top: 5px;
    }
    img.multi-col-img {
      max-width: 80%;
      height: auto;
    
      &:hover {
        opacity: 0.5;
        -webkit-transition: all 500ms ease;
        -moz-transition: all 500ms ease;
        -o-transition: all 500ms ease;
        transition: all 500ms ease;
      }
    }
  }
}


// Wifi-Network Page: Correct and Incorrect Wifi
.multi-column-panel.variant-2 {
  padding: 0;
  img.multi-col-img, .multi-col-icon {
    height: auto !important;
    padding: 0;
  }
  img.multi-col-img {
    width: 100%;
    margin-bottom: 25px;
  }
  img.multi-col-icon  {
    width: auto;
    margin-bottom: 25px;
  }
  h5 {
    margin-top: 0;
    margin-bottom: 0;
    text-transform: uppercase;
  }
  p {
    margin-top: 5px;
    color: #aaa;
  }

}

// Apps & Airplay: Airplay is Easy as 1-2-3
.multi-column-panel.variant-3 {
  h5 {
    margin-top: 0px;
  }
}


// Android: Android Apps Panel 1 and 2
.multi-column-panel.variant-4 {
  padding-bottom: 0;
}
.multi-column-panel.variant-5 {
  padding-top: 0;
}
.multi-column-panel.variant-4, .multi-column-panel.variant-5 {
  img.multi-col-img-large {
    max-height: 125px;
    width: auto;
  }
}

// Partner Programs
.multi-column-panel.variant-6 {
  img.multi-col-img {
    max-width: 100%;
  }
  h5 {
    margin-bottom: 15px;
  }
}

// Panels and Amps
.multi-column-panel.variant-7 {
  img.multi-col-icon {
    max-width: 100%;
  }
  h5 {
    margin-bottom: 15px;
  }
}


// Reduced Vertical Padding - Homepage Press Icons
.multi-column-panel.variant-8 {
  padding: 25px 0 10px;
}

@media screen and (min-width: 768px) {
  .multi-column-panel {
    .five-col {
      width: 20%;
    }
  }
}

@media screen and (min-width: 991px) {
  // Partner Program
  .multi-column-panel.variant-6 {
    img.multi-col-img {
      height: auto;
    }
    h5 {
      margin-top: 0;
    }
  }

  // Panels and Amps
  .multi-column-panel.variant-7 {
    img.multi-col-icon {
      height: 250px;
    }
  }
}


@media screen and (max-width: 992px) {
  .multi-column-panel {
    .col-md-3, .col-md-4 {
      .title, .body-text {
        height: auto !important; 
        min-height: initial !important;
      }
      p.body-text {
        margin-bottom: 25px;
      }
      a {
        display: block;
        margin-bottom: 30px;
      }
    }
    img.multi-col-img, img.multi-col-icon  {
      height: auto;
      max-width: 50%;
    }
  }

  .multi-column-panel.variant-2 {
    img.multi-col-img  {
      max-width: 100%;
    }
  }

}