// half-color-panel styles
.half-color-panel {
  text-align: center;
  padding: 100px 0 0;

  .container {
    position: relative;
    top: -80px;
  }

  h4 {
    margin-top: 0;
    margin-bottom: 0;
    line-height: 1.1em;

    &:first-of-type {
      margin-top: 50px;
      margin-bottom: 15px;
    }
  }

  h4 span {
    margin-left: 10px;
    font-size: 0.5em;
    font-weight: normal;
    // display: none;

    &:before {
      content: '/';
      font-size: 1.8em;
    }
  }

  h5 {
    position: relative;
    margin-top: 5px;
    margin-bottom: 0px;
  }

  img {
    position: relative;
    max-width: 100%;
  }

  p.text:first-of-type {
    margin-top: 0;
  }

  p.text:last-of-type {
    margin: auto;
    margin-bottom: 25px;
    position: relative;
  }

}



@media screen and (min-width: 768px) {
  .half-color-panel {
    padding-top: 75px;
    
    .container {
      top: -60px;
    }

    h4 span {
      display: inline;
    }
  }
}

@media screen and (max-width: 500px) {
  .half-color-panel {
    h4 {
      font-size: 1.5em;
    }
  }
}

