// header styles
header {
  width: 100%;
  position: fixed;
  background: white;
  z-index: 200;
  padding: 15px 0 10px;
  box-shadow: 0px 0px 2px #888;

  .desktop-nav, .mobile-nav { text-align: right; }

  .desktop-nav {
    padding: 0;
    display: none;      

    ul {
      margin-top: 13px;
      padding-left: 0;
    }

    li {
      display: inline;
      padding: 0 15px;
    }

    ul > li > a {
      font-size: 0.8em;
    }
  }

  .logo {
    padding: 10px 0;
    width: 150px;
  }

  .mobile-nav {
    ul { display: none; }

    .mobile-nav-icon {
      position: relative;
      top: 10px;
    }
  }

  // Overlay Menu in Mobile View
  .mobile-nav-popup {
    position: fixed;
    overflow: auto;
    display: none;
    left: 0; right: 0; top: 0; bottom: 0;
    padding-top: 150px;
    z-index: 300;
    background: rgba(88, 167, 210, 0.95);
    list-style: none;

    .logo-white {
      position: absolute;
      left: 25px;
      top: 40px;
      width: 200px;
    }

    #close-btn {
      position: fixed;
      top: 25px;
      right: 25px;
      color: white;
      font-size: 36px;
      &:hover {cursor: pointer;}
    }

    .mobile-nav-link {
      width: 100%;
      padding: 7.5px 0;
      
      a {
        color: white;
        font-size: 28px;
        padding: 7.5px 0;
        span { position: relative; top: -2px; padding-left: 2px; font-size: 8px; color: white; }
      }

      &:last-of-type {
        padding-bottom: 10px;
      }

      ul {
        margin-top: 5px;

        li {
          padding: 5px 0;
        }

        a { font-size: 22px;}
      }
    }

    ul {
      list-style: none;
    }
    li > ul {
      display: none;
    }
  }
}

.header-dropdown {
  position: fixed;
  display: none;
  top: 80px;
  width: 100%;
  font-size: 16px;
  padding: 35px 0;
  z-index: 100;
  background: rgba(88, 167, 210, 0.95);

  a { 
    color: white; 
    &:hover {
      opacity: 1;
    }
  }
  
  img { 
    display: block;
    max-width: 100%;
    width: 100px; 
    margin-bottom: 15px;
  }

  .subnav-title {
    font-weight: bold;
    text-transform: uppercase;
  }

  .subnav-subtitle {
    display: block;
    color: #eee;
    padding-top: 5px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: normal;
  }
}

@media screen and (min-width: 450px) {
  header {
    .logo {
      width: 225px;
    }
  }
}


@media screen and (max-width: 992px) {
  .header-dropdown {
    display: none !important;
  }
}

@media screen and (min-width: 992px) {
  header {

    .desktop-nav {
      display: block;
    }

    .mobile-nav {
      display: none;
    }
  }
}