// cta-box styles
.cta-box-container {
  padding: 35px 0 0px;
  text-align: center;

  h2, h5 {
    text-align: center;
  }

  img.image {
    max-width: 100%;
    margin-bottom: 25px;
  }

  .cta-box-background {
    background: #eee;
    padding: 15px 0 25px;
  }

  .cta-box {
    width: 100%;
    margin: auto;
    padding: 25px 40px;
    overflow: auto;
    background: #eee;
    border-radius: 10px;

    p {
      width: 90%;
      margin: auto;
      margin-bottom: 25px;
      text-align: center;
    }

    .cta-button {
      display: block;
      text-align: center;
      width: 100%;
      margin: auto; 
      padding: 15px 0px;
      border-radius: 10px;
      font-size: 16px;

      span {
        padding-left: 5px;
      }
    }
  }

  p.footer-text {
    text-align: center;
    font-size: 14px;
    a {
      color: rgb(252, 51, 243);
      font-size: 14px;
      font-weight: bold;
    }
  }

  .cta-button {
    color: white;
    font-weight: bold;
    background: rgb(252, 51, 243);
  }

}

.cta-box-container.variant-2 {
  padding-top: 0;
  .cta-box {
    img.icon { float: left; margin-right: 20px; }
  }

}


// Variant 3 - No title or text. Only CTA box
.cta-box-container.variant-3 {
  padding: 0;
}


// Variant 4 - Pricing Page No Vertical Padding at All
.cta-box-container.variant-4 {
  padding-top: 0 !important;
}

@media screen and (min-width: 768px) {
 .cta-box-container {
  .cta-box {
    .cta-button {
      width: 200px;
    }
  }
 } 
}

@media screen and (min-width: 991px) {

  .cta-box-container {
    padding-bottom: 60px;
    .cta-box-background {
      padding: 0;
      background: white;
    }

    .cta-box {
      width: 90%;
      p {
        width: 60%;
        float: left; 
        text-align: left;
        margin-bottom: 0;
      }
      .cta-button {
        margin-top: 0px;
        width: auto;
        display: inline;
        float: right;
        padding: 15px 25px;
      }
     
    }
  }

  .cta-box-container.variant-2 {
    padding-top: 75px;
    .cta-box {
      img.icon { float: left; margin-right: 20px; }
      p { width: 45%; }
      }
  }

  .cta-box-container.variant-3 {
    padding: 60px 0;
  }

}