// bg-video-panel styles

.bg-video-panel {
  height: 350px;

  #home-bg-video {
    height: 700px;
  }

  .bg-video-text {
    position: relative;
    top: 60px;

    h1 {
      color: white;
    }

  }
}

.bg-video-panel-img {
  background-size: cover !important;
  position: relative;
  width: 100%;
  height: 800px;
  img {
    max-width: 100%;
  }
}

@media screen and (max-width: 767px) {

  .bg-video-panel {
    text-align: center;

    video {
      display: none !important;
    }

    .bg-video-text {

      a.button {
        border: 2px solid black !important;
      }
      a.button, h1 {
        color: black !important;
      }
      h1 { margin-bottom: 50px; }
    }
    #home-bg-video > div {
      background-image: none !important;
    }

  }
}

@media screen and (max-width: 767px) {
  .bg-video-panel {
    height: auto;

    #home-bg-video {
      height: auto;
    }

    .bg-video-text {
      top: 10px;
      padding-bottom: 50px;
      border-bottom: 2px solid #ccc;
      padding-bottom: 100px;
    }
  }
}




@media screen and (min-width: 768px) {
  .bg-video-panel {
    height: 700px;
    .bg-video-text {
      @include vertical-align($translateY: -50%);
    }
  }

  .bg-video-panel-img {
    display: none;
  }

}
