// hero-panel styles

.hero-panel {
  position: relative;
  height: 800px;
  -webkit-background-size: cover !important;
  -moz-background-size: cover !important;
  -o-background-size: cover !important;
  background-size: cover !important;

  .hero-panel-overlay, .hero-panel-content-wrapper {
    height: 800px;
  }


  .hero-panel-content-wrapper{
    position: relative;
    margin: 0;
    color: white;
  }


  .hero-panel-content{
    @include vertical-align($translateY: -60%);
  }

  h3 { margin-bottom: 10px; }

  p.text {
    margin-bottom: 50px;
  }

  p.small-text {
    font-size: 0.8em;
  }

  .icon:first-of-type {
    margin-right: 25px;
  }

  a.button {
    color: white;
    white-space: normal;
    &:first-of-type {
      margin-right: 15px;
    }
  }

  .slick-dots li button:before {
    color: black;
  }

}




// Variant #2 - Background Video & Product Feature Panel 1
.hero-panel.variant-2 {
  text-align: center;

  .hero-panel-content {
    top: 250px;
  }
}


// Variant #3 - Background Image Panel becomes Two Sections on Top of Each Other
.hero-panel.variant-3, .hero-panel.variant-5  {
  height: 700px;

  .hero-panel-overlay, .hero-panel-content-wrapper {
    height: 700px;
  }
  .hero-mobile-section {
    max-height: 600px;
    img {
      max-width: 100%;
    }
  }
  a.button {
    padding: 7px 20px 10px;
    border: 3px solid white;
    border-radius: 10px;
  }  
}


// Variant #4 - Product Features: Airhome Speakers
.hero-panel.variant-4 {
  height: auto;
  text-align: center;
  
  .hero-mobile-section {
    height: 100%;
    img {
      max-width: 100%;
      width: 100%;
    }
  }
  .hero-panel-overlay, .hero-panel-content-wrapper {
    height: 400px;
  }

  .hero-panel-content-container {
    display: none;
  }

  .hero-panel-content {
    @include vertical-align($translateY: 0%);
    top: 50px;
  }

  .hero-panel-content-mobile {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  h4 {
    width: 50%;
    font-size: 0.8em;
    margin: auto auto;
    margin-top: 30px;
  }

}



// Variant #6 - Wall Mounting: Centered Background Images
.hero-panel.variant-6 {
  height: 700px;
  .slick-dots {
    bottom: -50px;
  }
  .hero-mobile-section {
    img {
      max-width: 100%;
      width: 100%;
    }
  }
  background-position: center center !important;
  background-color: #f3f2f0 !important;
}


@media screen and (min-width: 375px) {
  .hero-panel.variant-4 {
    h4 {
      font-size: 0.9em;
      margin-top: 40px;
    }
  }
}

@media screen and (min-width: 450px) {
  .hero-panel.variant-4 {
    h4 {
      font-size: 1.1em;
      margin-top: 50px;
    }
  }
}

@media screen and (min-width: 550px) {
  .hero-panel.variant-4 {
    h4 {
      font-size: 1.1em;
      margin-top: 70px;
      padding-right: 15px;
    }
  }
}


@media screen and (min-width: 650px) {
  .hero-panel.variant-4 {
    h4 {
      font-size: 1.3em;
      margin-top: 85px;
    }
  }
}

@media screen and (max-width: 768px) {
  .hero-panel {
    .hero-panel-content {
      @include vertical-align($translateY: -50%);
      text-align: center;
    }

    a.button {
      margin-bottom: 15px;
      width: 100%;
    }
  }
}

@media screen and (min-width: 768px) {
  .hero-panel.variant-4 {
    height: 427px;
    h4 {
      margin-top: 100px;
    }
  }
}

@media screen and (min-width: 850px) {
  .hero-panel.variant-4 {
    h4 {
      font-size: 1.4em;
      margin-top: 115px;
    }
  }
}


@media screen and (min-width: 991px) {
  // Variant #4 - Product Features: Airhome Speakers
  .hero-panel.variant-4 {
    height: 400px;
    h4 {
      margin-top: 80px;
      padding-left: 50px;
      width: 575px;
    }
  }

  // Variant #3 - Background Image Panel becomes Two Sections on Top of Each Other
  .hero-panel.variant-3, .hero-panel.variant-5  {
    .hero-mobile-section {
      display: none;
    }
    .hero-panel-content-wrapper {
      display: block;
    }
  }

  .hero-panel.variant-4 {
    .hero-mobile-section {
      display: none;
    }
    .hero-panel-content-container {
      display: block;
    }
  }

  // Variant #6 - Wall Mounting: Background Images
  .hero-panel.variant-6 {
    .hero-mobile-section {
      display: none;
    }
  }


}

@media screen and (max-width: 991px) {
  // Variant #3, #5 - Background Image Panel becomes Two Sections on Top of Each Other
  // Difference is that Variant #5 has Col-Md-5
  .hero-panel.variant-3, .hero-panel.variant-5 {
    height: auto;
   .hero-panel-content-wrapper {
      height: auto;
      padding-top: 15px;
    }
    text-align: center;
    background-image: none !important;
    background: #eee !important;
    padding-bottom: 75px;

    h2 { color: #666 !important; }
    .hero-panel-content {
      top: 0;
      transform: translateY(0%);
      -webkit-transform: translateY(0%);
    }
    h5.text {
      color: #666 !important;
    }
    a.button {
      color: #666 !important;
      border: 3px solid #666 !important;
    }
  }

  .hero-panel.variant-4 {
    background-position: center top !important;
    background-size: contain !important;
    background-color: #eee !important;
  }

  .hero-panel.variant-6 {
    height: auto;
    background-image: none !important;
  }
}