// two-col-panel styles
.two-col-panel {
  padding: 50px 0 60px;
  overflow: hidden;

  .two-col {
    height: auto;
  }

  .label {
    width: 300px;
    clear: both;
    margin: auto;

    span.number {
      float: left;
      font-weight: bold;
      margin-right: 20px;
    }
    .text-wrap {
      float: left;
    }
    p { 
      margin: 0;
      &:last-of-type {
        margin-bottom: 25px;
      }
    }
  }

  .inline-img {
    max-width: 300px;
  }

  .img-wrapper {
    width: 100%;
    text-align: center;
  }

  img.two-col-img {
    max-width: 100%;
    padding: 0;
    margin: auto;
  }

  img.icon-img {
    max-width: 100%;
  }

  h1.title-middle {
    text-align: center;
  }
  h1, h2 {
    margin-top: -10px;
  }
  h3 {
    margin-top: 0;
  }

  figcaption {
    // text-align: center;
  }

  .btns {
    padding-top: 20px;
    text-align: center;
  }
  
  .small-text {
    font-size: 0.8em;
  }

  .small-text-italic {
    font-size: 0.8em;
    font-style: italic;
    margin-top: 25px;
    margin-bottom: 0;
  }

  a.button {
    margin-right: 15px;
    margin-bottom: 15px;
    text-align: center;
  }

}

// Variant 2 - Product Features: Airhome Multiroom Audio System
.two-col-panel.variant-2 {
  h2 {
    margin-bottom: 15px;
  }
  h3 {
    margin-bottom: 5px;
  }
  p {
    margin-top: 5px;
  }
}

// Variant 3 - Product Features: AMPs
.two-col-panel.variant-3 {
  padding-bottom: 0;
  img.two-col-img {
    padding-bottom: 0;
  }
}


// Variant 4 - Product Features: But it all start with the panel
.two-col-panel.variant-4 {
  padding: 100px 0 0;

  img.two-col-img {
    padding-bottom: 0;
  }
  .img-wrapper-desktop {
    display: none;
  }
}

// Variant 5 - Two Images on Each Side w/ Titles 
.two-col-panel.variant-5 {
  padding: 100px 0 50px;
  img {
    margin-bottom: 50px;
  }
  .text {
    h2 {
      margin-top: 50px;
    }
    p {
      margin: 0;
      &:first-of-type { font-weight: normal; }
    }
  }
  .pre-title {
    font-size: 0.9em;
    display: block;
    margin-top: -25px;
    padding: 0;
    margin-bottom: 10px;
  }
}

// Variant 6 - Product Features: Airhome Audio Capabilities
.two-col-panel.variant-6 {
  padding: 100px 0;
  h5 {
    margin-bottom: 0;
  }
  p { 
    margin: 0;
    // font-weight: lighter; 
  }
  .dashed-lines-wrapper img { display: none; }
}



// Variant 7 - Wifi Network: Check Marks
.two-col-panel.variant-7 {
  .paragraph-checkmark {
    float: left;
    margin: auto;
    margin-bottom: 25px;
    width: 100%;
    text-align: left;
    &:before {
      float: left;
      content: url('../images/wifi-network/check-icon.png');
      margin-right: 35px;
      position: relative;
      top: -15px;
    }
  }
}


// Variant 9 - Apps Airplay: Check Marks
.two-col-panel.variant-9 {
  .slick-slider {
    margin-top: 100px;
  }

  .paragraph-checkmark-wrapper {
    float: left;
    margin-top: 35px;
  }
  .paragraph-checkmark {
    float: left;
    margin: auto;
    margin-bottom: 25px;
    width: 100%;
    text-align: left;
    &:before {
      float: left;
      content: url('../images/wifi-network/check-icon.png');
      margin-right: 35px;
      position: relative;
      top: -15px;
    }
  }
}


// Variant 10 - Panels & Amps: Airhome is modular
.two-col-panel.variant-10 {
  img.two-col-img.img-right-desktop {
    display: none;
  }
}


@media screen and (min-width: 768px) {
  .two-col-panel {
    a.button {
      display: inline;
    }
    .inline-img {
      margin: 0;
    }
  }
  // Variant 7 - Wifi Network: Check Marks
  .two-col-panel.variant-7 {
    .paragraph-checkmark {
      width: 50%;
    }
  }
}



@media screen and (min-width: 992px) {
  
  .two-col-panel {
    padding: 50px 0 45px;
    .text {
      position: relative;
    }
    .btns {
      text-align: left;
    }
    img.two-col-img {
      max-width: 100%;
      // max-height: 500px;
      padding: 0;
    }

    .label {
      padding-left: 50px;
      width: 100%;
    }

  }

  .two-col-panel.variant-3 {
    img.two-col-img {
      max-height: 600px;
    }

    .col-xs-12.col-md-6.two-col:first-of-type {
      float: right;
    }

    .col-xs-12.col-md-6.two-col:last-of-type {
      float: left;
    }
  }

  .two-col-panel.variant-3, .two-col-panel.variant-4 {
    height: 600px;
    padding: 0;
    margin-bottom: 0 !important;

    .container, .img-wrapper, .two-col {
      height: 600px;
    }
    .text {
      @include vertical-align($translateY: -50%);
    }
    img.two-col-img {
      left: 0;
      bottom: 0 !important;
      position: absolute;
      padding: 0;
      min-width: 350px;
    }
  }

  // Variant 4 - Product Features: But it all start with the panel
  .two-col-panel.variant-4 {
    img.two-col-img {
      min-width: 734px;
    }
    .img-wrapper-mobile {
      display: none;
    }
    .img-wrapper-desktop {
      display: block;
    }
  }


  // Variant 5 - Two Images on Each Side w/ Titles 
  .two-col-panel.variant-5 {
    .text {
      height: 200px;
      h2 { margin-top: 0; }
    }
    img {
      margin-bottom: 0;
    }
  }


  // Variant 6 - Product Features: Airhome Audio Capabilities
  .two-col-panel.variant-6 {
    img {
      margin-top: 25px;
    }
    .dashed-lines-wrapper img {
      position: absolute;
      display: block;
      top: 0px;
    }
    .dashed-text {
      margin-left: 125px;
      &:first-of-type {
        margin-left: 65px;
      }
    }
    .dashed-text-last {
      margin-left: 10px;
    }
  }


  // Variant 7 - Wifi Network: Check Marks
  .two-col-panel.variant-7 {
    .two-col:first-of-type > div {
      top: 100px;
    }
    .paragraph-checkmark {
      width: 100%;
    }
  }


// Variant 10 - Panels & Amps: Airhome is modular
.two-col-panel.variant-10 {
  img.two-col-img.img-right-desktop {
    display: block;
  }
  img.two-col-img.img-right-mobile {
    display: none;
  }
  .first-col {
    float: right;
  }
}

}

@media screen and (max-width: 991px) {
  .two-col-panel {
    .text {
      top: 0 !important;
    }

    .two-col {
      padding: 0;
    }
  }

  .two-col-panel.variant-4 {
    img.two-col-img {
      // max-height: 430px;
    }
  }
}